.burger {
  z-index: initial;
  width: 30px;
  height: 16px;
  cursor: pointer;
  background-color: rgba(0, 0, 0, 0);
  border: none;
  padding: 10px;
  display: none;
  position: relative;
}

@media only screen and (max-width: 1279px) {
