}

.burger__line {
  z-index: initial;
  width: 100%;
  height: 2px;
  border-radius: 1px;
  transition: opacity .25s cubic-bezier(.4, 0, .2, 1);
  position: absolute;
  top: 50%;
  left: 0;
  transform: translateY(-50%);
}

body.light-theme .burger__line {
  color: #fcfcfc;
  background: #202025;
}

body.dark-theme .burger__line {
  color: #202025;
  background: #fcfcfc;
}

.burger:before, .burger:after {
  content: "";
  z-index: initial;
  width: 100%;
  height: 2px;
  background-color: #000;
  border-radius: 1px;
  transition: transform .25s cubic-bezier(.4, 0, .2, 1), top .25s cubic-bezier(.4, 0, .2, 1), bottom .25s cubic-bezier(.4, 0, .2, 1);
  position: absolute;
  left: 0;
}

body.light-theme .burger:before, body.light-theme .burger:after {
  color: #fcfcfc;
  background: #202025;
}

body.dark-theme .burger:before, body.dark-theme .burger:after {
  color: #202025;
  background: #fcfcfc;
}

.burger:before {
  top: 0;
}

.burger:after {
  bottom: 0;
}

.burger-active .burger__line {
  opacity: 0;
}

.burger-active:before {
  top: 50%;
  transform: rotate(135deg);
}

.burger-active:after {
  top: 50%;
  bottom: auto;
  transform: rotate(-135deg);
}

